@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.intro {
  position: absolute;
  top: 19%;
  left: 10%;
  z-index: 1;
  height: 60%;
  width: 36%;
  display: flex;
  justify-content: center;
}

.typewriter {
  height: 20%;
}

.typewriter h1 {
  font-size: 4vw;
  font-family: 'Josefin Sans', sans-serif;
  color: rgb(255, 255, 255);
  overflow: hidden; 
  border-right: .15em solid orange;
  white-space: nowrap;
  margin: 0 auto; 
  letter-spacing: .15em;
  -webkit-animation: 
    typing 1.5s steps(40, end),
    blink-caret .5s step-end infinite;
          animation: 
    typing 1.5s steps(40, end),
    blink-caret .5s step-end infinite;
}

.typewriter span {
  color: #E31B6D;
}

@-webkit-keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

@-webkit-keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: orange }
}

@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: orange }
}

.intro-text  {
  position: absolute;
  top: 30%;
  left: 10%;
  height: 30%;
  width: 40%;
  display: flex;
  justify-content: center;
  font-family: 'Josefin Sans', sans-serif;
}

.intro-text p {
  color: rgb(68, 255, 239);
  font-size: 2vw;
  letter-spacing: 1.5px;
  line-height: 7vh;
  -webkit-animation: text 3s 1;
          animation: text 3s 1;
}

.intro-text span {
  color: #E31B6D;
}


@-webkit-keyframes text {
    0% {
      opacity: 0;
    }
    10% {
      opacity: 0.1;
    }
    20% {
      opacity: 0.2;
    }
    30% {
      opacity: 0.3;
    }
    40% {
      opacity: 0.4;
    }
    50% {
      opacity: 0.5;
    }
    60% {
      opacity: 0.6;
    }
    70% {
      opacity: 0.7;
    }
    80% {
      opacity: 0.8;
    }
    90% {
      opacity: 0.9;
    }
    100% {
      opacity: 1;
    }
}


@keyframes text {
    0% {
      opacity: 0;
    }
    10% {
      opacity: 0.1;
    }
    20% {
      opacity: 0.2;
    }
    30% {
      opacity: 0.3;
    }
    40% {
      opacity: 0.4;
    }
    50% {
      opacity: 0.5;
    }
    60% {
      opacity: 0.6;
    }
    70% {
      opacity: 0.7;
    }
    80% {
      opacity: 0.8;
    }
    90% {
      opacity: 0.9;
    }
    100% {
      opacity: 1;
    }
}


