* {
  border: 0;
  margin: 0;
  padding: 0;
}


body {
    background: #100e17;
}


.wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 98vw;
  height: 100%;
  padding-top: 40px;
  object-fit: cover;
  padding-bottom: 40px;
}


.card {
  position: relative;
  grid-area: card;
  align-self: center;
  justify-self: center;
  overflow: hidden;
  display: block;
  width: 100%;
  max-width: 350px;
  height: 400px;
  font-size: 1rem;
  font-size: var(--fontOrigin);
  border-radius: 8px;
  box-shadow: 0 16px 60px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  -webkit-transition: all 0.60s ease;
  -webkit-transition: all var(--speed) ease;
  transition: all 0.60s ease;
  transition: all var(--speed) ease;
  margin-left: 5%;
  margin-bottom: 40px;
}

.card:hover {
  box-shadow: 0 40px 130px rgba(0, 0, 0, 0.6);
  -webkit-transform: scale(1.1, 1.1);
  transform: scale(1.1, 1.1);
}

.card__save {
  position: absolute;
  top: 10px;
  right: 10px;
  display: block;
  width: 35px;
  height: 35px;
  background: transparent;
  border: 0;
  opacity: 0;
  border-radius: 3px;
  -webkit-transition: all 0.60s ease;
  -webkit-transition: all var(--speed) ease;
  transition: all 0.60s ease;
  transition: all var(--speed) ease;
}

.card__save:focus {
  outline: 0;
  background-color: rgba(255, 255, 255, 0);
}

.card:hover .card__save {
  opacity: 0.6;
}

.card__save i {
  font-size: 18px;
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  -webkit-transition: all 0.60s ease;
  -webkit-transition: all var(--speed) ease;
  transition: all 0.60s ease;
  transition: all var(--speed) ease;
}

.card__save:hover i {
  color: rgba(255, 255, 255, 0.8);
}

.card__save:active,
.card__save.active {
  opacity: 1 !important;
}

.card__save:active i,
.card__save.active i {
  color: white;
}

.card__image {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  overflow: hidden;
  display: block;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 1)), to(rgba(0, 0, 0, 0.5)));
  background: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.5));
}

.card__image img {
  -webkit-transform: scale(1.3, 1.3);
  transform: scale(1, 1);
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 1;
  -webkit-transition: all 0.60s ease;
  -webkit-transition: all var(--speed) ease;
  transition: all 0.60s ease;
  transition: all var(--speed) ease;
  -webkit-animation-name: zoom;
          animation-name: zoom;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.card:hover .card__image img {
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
  opacity: 0.5;
}

.card__header {
  display: grid;
  width: 100%;
  height: 200px;
}

.card__profile {
  align-self: center;
  justify-self: center;
  display: block;
  overflow: hidden;
  width: 10vmax;
  height: 10vmax;
  max-width: 100px;
  max-height: 100px;
  border-radius: 50%;
}

.card__profile img {
  -webkit-transform: scale(1.5, 1.5)  translateZ(0);
  transform: scale(1.1, 1.1)  translateZ(0);
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 50%;
  -webkit-filter: grayscale(50%) contrast(75%) brightness(1.3);
  filter: grayscale(50%) contrast(75%) brightness(1.3);
  -webkit-transition: all 0.60s ease;
  -webkit-transition: all var(--speed) ease;
  transition: all 0.60s ease;
  transition: all var(--speed) ease;
  mix-blend-mode: normal;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.card:hover .card__profile img {
  -webkit-transform: scale(1, 1)  translateZ(0);
  transform: scale(1, 1)  translateZ(0);
}

.card__body {
  display: grid;
  padding: 0 20px;
}

.card__name {
  align-self: center;
  justify-self: center;
  margin-bottom: 2px;
  color: white;
  font-size: 1.375em;
  font-size: var(--fontLarge);
  font-weight: 500;
  font-weight: var(--fontLight);
  letter-spacing: 0.1rem;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 0 1px 2px rgba(255, 255, 255, 0.35);
  background: -webkit-linear-gradient(rgb(0, 0, 0), #000000);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-transition: all 0.60s ease;
  -webkit-transition: all var(--speed) ease;
  transition: all 0.60s ease;
  transition: all var(--speed) ease;
}

.card:hover .card__name {
  background: -webkit-linear-gradient(white, #c1c1c1);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.card__job {
  align-self: center;
  justify-self: center;
  display: inline-block;
  font-size: 0.925em;
  font-size: var(--fontSmall);
  font-weight: 500;
  font-weight: var(--fontLight);
  text-align: center;
  letter-spacing: 0.12rem;
  color: rgb(255, 4, 222);
  -webkit-transition: all 0.60s ease;
  -webkit-transition: all var(--speed) ease;
  transition: all 0.60s ease;
  transition: all var(--speed) ease;
}

.card:hover .card__job {
  color: rgb(92, 131, 221);
}

.card__bio {
  position: relative;
  -webkit-transform: translateY(30%);
  transform: translateY(30%);
  display: block;
  margin: 22px 0 16px 0;
  font-size: var(--fontMedium);
  font-weight: 500;
  font-weight: var(--fontLight);
  color: rgba(255, 255, 255, 0.65);
  opacity: 0;
  -webkit-transition: all 0.60s ease;
  -webkit-transition: all var(--speed) ease;
  transition: all 0.60s ease;
  transition: all var(--speed) ease;
  text-align: center;
}

.card:hover .card__bio {
  -webkit-transform: translateY(0);
  transform: translateY(0);
  opacity: 1;
}

